.c-cover {
  display: flex;
  font-family: sans-serif;
  height: 360px;

  &__picture {
    flex-grow: 1;
    background: url("../assets/images/summer.png") no-repeat;
    background-position: center;
    background-size: cover;
    width: 30%;

    @include bp($md) {
      background-position: left;
    }
  }

  &__text {
    margin-bottom: 0;
    color: $dark-gray;
  }

  &__body {
    display: flex;
    flex-direction: column;
    justify-content: center;
    flex-grow: 1;
    background: $white;
    padding: 30px;

    @include bp($md) {
      padding: 50px;
    }
  }

  &__title {
    font-family: 'PT Serif', serif;
    font-style: italic;
    font-weight: 100;
    font-size: 40px;
    margin-top: 0px;
    text-transform: capitalize;
  }
}
